.static-txt {
  font-size: 35px;
  color: #fff;
  font-weight: 400;
}


ul.dynamic-text li {
  list-style: none;
  position:relative;
  top:0;
  -webkit-animation: slide 6s steps(4) infinite;
          animation: slide 6s steps(4) infinite;
}

@-webkit-keyframes slide{
  100%{
    top:-265px;
  }
}

@keyframes slide{
  100%{
    top:-265px;
  }
}

ul.dynamic-text li {
  font-size: 24px;
  color: #fc6d6d;
}

ul.dynamic-text li span:after {content: "";position: absolute;height: 7%;border-left: 2px solid;left: 48rem;
  background-color: #535353;
  width: 150px;
  -webkit-animation: typing 1.5s steps(10) infinite;
          animation: typing 1.5s steps(10) infinite;}



@-webkit-keyframes typing{
    100%{
      right: 100%;
      margin: 0 -35px 0 100px;
    }
}



@keyframes typing{
    100%{
      right: 100%;
      margin: 0 -35px 0 100px;
    }
}


